import React from 'react';
import './intro.css';
import Particles from 'react-particles-js';
import 'tachyons';

const particles = {
  "particles": {
     "number": {
       "value": 80,
       "density": {
         "enable": true,
         "value_area": 1000
       }
     },
     "color": {
       "value": ["#BD10E0","#B8E986","#50E3C2","#FFD300","#E86363"]
     },
     "shape": {
       "type": "circle",
       "stroke": {
         "width": 0,
         "color": "#b6b2b2"
       }
     },
     "opacity": {
       "value": 0.5211089197812949,
       "random": false,
       "anim": {
         "enable": true,
         "speed": 1,
         "opacity_min": 0.1,
         "sync": false
       }
     },
     "size": {
       "value": 8.017060304327615,
       "random": true,
       "anim": {
         "enable": true,
         "speed": 12.181158184520175,
         "size_min": 0.1,
         "sync": true
       }
     },
     "line_linked": {
       "enable": true,
       "distance": 150,
       "color": "#c8c8c8",
       "opacity": 0.4,
       "width": 1
     },
     "move": {
       "enable": true,
       "speed": 1,
       "direction": "none",
       "random": false,
       "straight": false,
       "out_mode": "bounce",
       "bounce": false,
       "attract": {
         "enable": false,
         "rotateX": 600,
         "rotateY": 1200
       }
     }
   },
   "interactivity": {
     "detect_on": "canvas",
     "events": {
       "onhover": {
         "enable": true,
         "mode": "repulse"
       },
       "onclick": {
         "enable": false,
         "mode": "push"
       },
       "resize": true
     },
     "modes": {
       "grab": {
         "distance": 400,
         "line_linked": {
           "opacity": 1
         }
       },
       "bubble": {
         "distance": 400,
         "size": 40,
         "duration": 2,
         "opacity": 8,
         "speed": 3
       },
       "repulse": {
         "distance": 80,
         "duration": 0.4
       },
       "push": {
         "particles_nb": 4
       },
       "remove": {
         "particles_nb": 2
       }
     }
   },
   "retina_detect": true
}

class Intro extends React.Component {
  render(){
    return(
      <div className='topContainer containerFlex'>
        <Particles className='particles' params={particles}/>
        <div className='nameplate br3 pa2 shadow-2'>
          <h1 className='noMargin'>James Kubitz</h1>
          <p className='blueText'>I design and develop ideas that become reality.</p>
        </div>
      </div>
    );
  }
}

export default Intro;
